import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import Add from './Add';
import Table from './Table';
class Item{
    constructor(task,done){
        this.task=task;
        this.done=done;
    }

}

export default class List extends React.Component{
    constructor(props){
        super(props);
        let prevList='';
        if(localStorage){
            prevList=JSON.parse(localStorage.todolist);
        }
        else{
            alert("Oops. Your browser does not support Local Storage. The list will be temporary");
        }
        this.state={items:prevList,input:''};
    }
    saveToStorage(arr){
        if(localStorage){
            localStorage.todolist=JSON.stringify(this.state.items);
            
        }
    }
    
    inputUpdate(event){
        
        this.setState({...this.state,input: event.target.value});
       
    }
    sbmt(){
        let arr=this.state.items;
        if(this.state.input==''){
            alert("Please enter a task");
            return;
        }
        let input=this.state.input;
        input=input.replace(input[0],input[0].toUpperCase());
        let newItem = new Item(input,false);
        arr.push(newItem);
        this.saveToStorage(arr);
        this.setState({...this.state, items: arr});
        
    }
    delete(event){
 
       
         let arr=this.state.items;
         let newArr=arr.filter((ele,i)=>{return i!=event.target.id});
         this.setState({...this.state,items: newArr,});
         if(localStorage){localStorage.todolist=JSON.stringify(newArr);}
    }
    checkItem(event){
        var arr=this.state.items;
        arr[event.target.id].done=!arr[event.target.id].done;
        if(localStorage){localStorage.todolist=JSON.stringify(arr);}
        this.setState({...this.setState, items: arr});
    }
    render(){
        return(
            <>  
            <Add submit={this.sbmt.bind(this)} input={this.inputUpdate.bind(this)}/>
            <br/>
            <Table items={this.state.items} delete={this.delete.bind(this)} checkbox={this.checkItem.bind(this)}/>
            </>
        );
    }
}